import React from 'react';
import Navigation from './Components/Nav.js';
import Logo from './Components/Logo.js'
import ImageSearch from './Components/ImageSearch'
import Rank from './Components/Rank';
import Register from './Components/Register';
import FaceR from './Components/FaceR';
import SignIn from './Components/SignIn'
import './App.css';
import 'tachyons';



const initialState = {
  input: '',
  imageURL:'',
  box: {},
  route: 'signin',
  isSignedIn: false,
  user: {
    id: '',
    name: '',
    email: '',
    entries: 0,
    joined: ''
  }

}
class App extends React.Component {
  constructor() {
    super();
    this.state = {
      input: '',
      imageURL:'',
      box: {},
      route: 'signin',
      isSignedIn: false,
      user: {
        id: '',
        name: '',
        email: '',
        entries: 0,
        joined: ''
      }
    }
  }

  onInChange = (event) => {
    this.setState({input: event.target.value});
  }

  loadUser = (data) => {
    this.setState({user: {
      id: data.id,
      name: data.name,
      email: data.email,
      entries: data.entries,
      joined: data.joined
    }})
  }

  boundingBox= (info) => {
    const detectedFace = info.outputs[0].data.regions[0].region_info.bounding_box;
    const image=document.getElementById('inputImg');
    const width=Number(image.width);
    const height=Number(image.height);
    console.log(width, height);
    return {
      leftCol: (detectedFace.left_col * width),
      rightCol: width - (detectedFace.right_col * width),
      topRow: (detectedFace.top_row * height),
      bottomRow: height - (detectedFace.bottom_row * height),
      }
  }

  makeBox = (box) => {
    console.log(box)
    this.setState({box: box})
  }

  onButtonSubmit = () => {
    this.setState({imageURL: this.state.input})
      fetch('http://localhost:3000/imageurl', {
        method: 'post',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify({
          input: this.state.input
        })
      })
      .then(response => response.json())
      .then(response => {
        if (response) {
          fetch('http://localhost:3000/image', {
            method: 'put',
            headers: {'Content-Type': 'application/json'},
            body: JSON.stringify({
              id: this.state.user.id
            })
          })
            .then(response => response.json())
            .then(count => {
              this.setState(Object.assign(this.state.user, { entries: count}))
            })
        }
        this.makeBox(this.boundingBox(response))
      })
      .catch(err=>console.log(err))
  }

  onRouteChange = (route) => {
    if (route==='signin') {
      this.setState(initialState)
    } else if (route==='home') {
      this.setState({isSignedIn: true})
    }
    this.setState({route: route});
  }

  render(){
    return (
      <div className="App">
      <Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/>
      { this.state.route === 'signin' 
        ? <div className='sign-in'>
            <SignIn loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
          </div>
        : (this.state.route ==='home'
          ? <div>
              <Logo />
              <Rank name={this.state.user.name} entries={this.state.user.entries} />
              <ImageSearch onInChange={this.onInChange} onButtonSubmit={this.onButtonSubmit}/>
              <FaceR box={this.state.box} url={this.state.imageURL} />
            </div>

          : <div className='sign-in'>
              <Register loadUser={this.loadUser} onRouteChange={this.onRouteChange} />
            </div>
          )

        
      }
      </div>        
    );
  }
}

export default App;
